<template>
  <div>
    <div class="remote-tuning-history">
      <el-card class="info-container">
        <div slot="header">
          <span>机组信息</span>
          <el-button
            :style="{
              position: 'absolute',
              right: '30px',
              top: '30px'
            }"
            @click="handleBackBtn"
          >
            返回
          </el-button>
        </div>
        <el-form disabled :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="机组名称">
            {{ formInline.DisplayName }}
          </el-form-item>
          <el-form-item label="云猫标识">
            {{ formInline.CollectorToken }}
          </el-form-item>
          <el-form-item label="控制器品牌">
            {{ formInline.ControllerBrand }}
          </el-form-item>
          <el-form-item label="调参协议">
            {{ formInline.ProtocolName }}
          </el-form-item>
          <el-form-item label="安装地点">
            {{ formInline.InstallationSite }}
          </el-form-item>
          <el-form-item label="通讯协议">
            {{ formInline.GenCoollectorName }}
          </el-form-item>
          <el-form-item label="有人值守">
            {{ formInline.IsOnduty ? '是' : '否' }}
          </el-form-item>
        </el-form> </el-card
      ><br />
      <table-list
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        :dataTotal="dataTotal"
        :pagination="paginationCfg"
        @handleChangePage="handleChangePage"
        @handleChangeSize="handleChangeSize"
        border
      ></table-list>
    </div>
  </div>
</template>

<script>
// import _data from './_data'
import { formatTimeDayjs } from '@/utils/common'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'

export default {
  name: 'RemoteTuningHistory',
  components: {},
  mixins: [paginationConfigMixin],
  data() {
    return {
      formInline: {
        DisplayName: '',
        CollectorToken: '',
        ControllerBrand: '',
        ProtocolName: '',
        InstallationSite: '',
        GenCoollectorName: '',
        IsOnduty: ''
      },
      dataSource: [],
      options: {
        height: '100%',
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 126 // 操作列宽度
        }
      },
      columns: [
        {
          prop: 'Name',
          label: '名称',
          width: 100
        },
        {
          prop: 'OldValue',
          label: '旧值',
          width: 100
        },
        {
          prop: 'NowValue',
          label: '新值',
          width: 100,
          render: (h, parmas) => {
            const row = parmas.row
            const n = row.NowValue,
              o = row.OldValue
            let r = []
            if (n.toString() === o.toString()) {
              r = [h('span', row.NowValue)]
            } else {
              r = [
                h(
                  'span',
                  {
                    style: 'color: red;fontSize:14px;fontWeight:bold'
                  },
                  row.NowValue
                )
              ]
            }
            return r
          }
        },
        {
          prop: 'Unit',
          label: '单位',
          width: 100
        },
        {
          prop: 'Create_Date',
          label: '更新时间',
          width: 100
        }
      ]
    }
  },
  props: ['myProp'],
  // inject: [''],
  // provide() {
  //   return {}
  // },
  computed: {},
  watch: {
    myProp: {
      handler: function (nv) {
        this.initData()
      },
      // 首次进入页面更新数据
      immediate: true
    }
  },
  methods: {
    queryTableData() {
      this.getHistoryList()
    },
    initData() {
      this.getInfo(), this.getHistoryList()
    },
    getInfo() {
      const query = this.myProp
      const p = {},
        q = {
          Id: query.No,
          generatorId: query.GeneratorSetId
        }
      this.$api.operation.getProtocolParmasByGeneatorIdForTuning(p, q).then((value) => {
        // value = _data.info
        const gensetInfo = value.Data && value.Data.genset
        if (gensetInfo) {
          this.formInline = {
            ...gensetInfo
          }
        }
      })
    },
    getHistoryList() {
      const query = this.myProp
      const p = {},
        q = {
          Id: query.No,
          generatorId: query.GeneratorSetId,
          page: this.paginationCfg.currentPage,
          pageSize: this.paginationCfg.pageSize
        }
      this.$api.operation.getProtocolParmasByGeneatorIdForTuningRecords(p, q).then((value) => {
        // value = _data.list
        const dataSource = new Array()
        const historyList = value.Data && value.Data.Data
        const TotalItemCount = value.Data && value.Data.TotalItemCount
        if (historyList && Array.isArray(historyList)) {
          historyList.forEach((e) => {
            const obj = {
              ...e
            }
            obj.Create_Date = formatTimeDayjs(obj.Create_Date).format('YYYY-MM-DD HH:mm')
            dataSource.push(obj)
          })
          this.dataTotal = TotalItemCount
          this.dataSource = dataSource
        }
      })
    },
    handleBackBtn() {
      this.$emit('changeIs', 'MainPage')
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>
<style lang='less' scoped>
/deep/ .el-form-item__label {
  font-weight: bold;
}
/deep/ .el-card__body {
  padding: 10px 20px;
}
</style>
<style lang='less'  scoped>
.remote-tuning-history {
  height: 100%;
}
/deep/ .table-form-container_content {
  height: calc(100% - 170px);
}
</style>


